@import url(https://fonts.googleapis.com/css?family=Raleway:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  color:#827E7E;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



nav{
    width:100%;
    position:fixed;
}
ul.navbar{
    display: flex;
    flex-direction: row !important;
    justify-content: flex-end;
    align-items: center;

}
.nav-item {
    padding:1rem 3rem;
}
.nav-link {
    font-weight:700;
    color:#fff;
}
header {
    height:80vh !important;
    width:100%;
    z-index:-1;
    display:flex;
    align-items:center;
    justify-content: center;
}
header img {
   
}
.background-header {
    position: absolute;
    width:100%;
    height:80vh;
    background-image:url(/react/static/media/wood-right.ae50cb0d.png),url(/react/static/media/wood-left.7d5b60c6.png);
    background-repeat: no-repeat;
    background-position:0% 0%,100% 0%;
    background-size: 50% 100%; 
    -webkit-backdrop-filter: grayscale(100%); 
            backdrop-filter: grayscale(100%);
    z-index:-1;
}
.background-header:after{
    content:" ";
    width:100%;
    height:100%;
    position:absolute;
    background-color:rgba(0,0,0,0.7);
}

.about-section,.shop-section{
    margin:auto;
    margin-top:3rem;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width:80%;
    height:80vh !important;
    overflow: hidden;
}
.team-section{
    width:100vw;
    height:100vh;
    position: relative;
    color:#fff;
    background-color: #1D1818;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.contact-section {
    width:70%;
    margin:auto;
    padding-top:8rem;
    font-size:18px;
    margin-bottom:5rem;
}
.footer-section {
    height:154px;
    background-color:#000;
    display:flex;
    justify-content: center;
    align-items: center;
    background-image:url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 200px auto;
}

li {
    list-style-type: none;
    margin:0 1rem;
}
.nav-link {
    text-decoration: none;
}
.section-container {
    width:50%;
    overflow: hidden;
}

.section-text{
    text-align:left;
    align-items: flex-start;
    justify-content: center;
}
.section-img{
    align-items: center;
    justify-content: center;
}

.section-img-container{
    position: relative;
    width:60%;
    background-color: red;

}
.section-img-container::before {
    content:"";

    position: absolute;
    width:100%;
    height:100%;
    top:-41px;
    left:-84px;
    background:rgba(188, 154, 68, 0.4);
    z-index:-1;
}

.section-img img{    
   width:100%;
   height:auto;
   display:block; 
   padding-right:5rem;
}

.section-title{
    font-weight: 500    ;
    font-size:24px;
    line-height: 28px;
    letter-spacing: 10%;
    color:#000;
}
.section-title:before {
    content:" ";
    white-space: pre;
    margin-right:13px;
    width:18px;
    height: 44px;
    background:#E4D7B4;
}
.section-content {
    font-size:18px;
    line-height: 28px;
    padding-right:4rem;
    margin-bottom:5rem;
}
.btn-section {
    position: relative;
    font-size:18px;
    padding:1rem 3rem;
    background: none;
    border:2px solid #BC9A44;
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
}
.btn-section:hover {
    background-color:  #BC9A44;
    color:#fff;
    border:2px solid #000;    
}
.btn-section::after{
    content:" ";    
    left:95%;
    top:48%;
    white-space: nowrap;
    display:inline;
    position:absolute;
    width:150px;
    border-top:2px solid #000;
}
